<template>
  <div>
    <div class="custom-search js-search-bar" :class="{focus:focus || searchValue}">
      <form @submit="submit">
        <input
          type="search"
          class="custom-search-input"
          v-model="searchValue"
          @focus="focusInput"
          @blur="blurInput"
          :placeholder="searchForm.placeholder"
        >
        <span class="icon-custom-search"></span>
      </form>
      <button
        class="custom-search-type-selection-btn custom-search-type-selection-btn-focus"
        @click="showNavBtn"
        v-show="focus || searchValue"
      >{{searchForm.button}}</button>
      <span class="cancel" v-show="focus || searchValue" @click="goSort">取消</span>
      <div class="search-type-dropdown-menu" v-show="showNavButton">
        <span class="search-type-dropdown-menu-indicator">
          <i></i>
        </span>
        <div class="drop-menu-content">
          <div class="search-type-goods" @click="chooseGoods">
            <i class="icon-goods"></i>
            <span>商品</span>
          </div>
          <div class="search-type-dropdown-menu-divider"></div>
          <div class="search-type-shop" @click="chooseShops">
            <i class="icon-shop"></i>
            <span>店铺</span>
          </div>
        </div>
      </div>
    </div>
    <div class="search-content">
      <!-- 历史搜索 待做-->
    </div>
  </div>
</template>
<script>
export default {
  props: ["showId"],
  data() {
    return {
      focus: false,
      searchValue: "",
      showNavButton: false,
      searchForm: {
        value: "测试商品",
        placeholder: "搜索更赞的商品",
        button: "商品"
      },
      id: 0
    };
  },

  mounted() {
    if (this.showId) {
      this.id = this.showId;
    } else if (this.$route.query.showId) {
      this.id = this.$route.query.showId;
    } else {
      this.id = 0;
    }
    this.searchValue = this.$route.query.keyword;
  },
  watch: {
    id(val) {
      if (val == 0) {
        this.chooseGoods();
      } else {
        this.chooseShops();
      }
    },
    searchValue(val) {
      if (val && val.trim()) {
        this.focus = true;
      }
    }
  },
  methods: {
    focusInput() {
      this.focus = true;
      this.$router.push({ name: "search" });
    },
    blurInput() {
      // this.focus = false;
    },
    showNavBtn() {
      this.showNavButton = true;
    },
    submit(e) {
      e.preventDefault();
      if (this.searchValue.trim()) {
        this.$router.push({
          name: "search",
          query: { showId: this.id, keyword: this.searchValue }
        });
      }
    },
    chooseGoods() {
      this.id = 0;
      this.showNavButton = false;
      this.searchForm = {
        value: "测试商品",
        placeholder: "搜索更赞的商品",
        button: "商品"
      };
      if (this.searchValue.trim()) {
        this.$router.push({
          name: "search",
          query: { showId: this.id, keyword: this.searchValue }
        });
      }
    },
    chooseShops() {
      this.id = 1;
      this.showNavButton = false;
      this.searchForm = {
        value: "测试店铺",
        placeholder: "搜索更赞的店铺",
        button: "店铺"
      };
      if (this.searchValue.trim()) {
        this.$router.push({
          name: "search",
          query: { showId: this.id, keyword: this.searchValue }
        });
      }
    },
    goSort() {
      this.searchValue = "";
      this.$router.push({ name: "sort" });
    }
  }
};
</script>
