<template>
  <div class="main-content search-page">
    <search-form :showId="showId"></search-form>
    <div class="content" @touchmove="move">
      <div class="search-wrap">
        <ul
          v-infinite-scroll="getGoods"
          :infinite-scroll-disabled="loading"
          infinite-scroll-distance="50"
          v-if="showId == 0"
        >
          <li v-for="good in goodLists" :key="good.name" class="goods-item">
            <a @click="$router.push({name:'goodDetail',params:{id:good.id}})">
              <div class="thumb">
                <img :src="good.img">
                <i class="sell-out" v-if="good.isOut"></i>
              </div>
              <div class="detail">
                <div class="title">{{good.name}}</div>
                <div class="meta relative">
                  <span class="price">￥{{good.price}}</span>
                  <span class="ship pull-right" v-if="good.isPostage">包邮</span>
                </div>
              </div>
            </a>
          </li>
        </ul>
        <ul
          v-infinite-scroll="getShops"
          :infinite-scroll-disabled="loading"
          infinite-scroll-distance="50"
          v-if="showId == 1"
        >
          <li v-for="shop in shopLists" :key="shop.name" class="shop-item">
            <div class="shop-title">
              <a href>{{shop.name}}</a>
              <span class="follows">/ 关注度: {{shop.likeDegree}}</span>
            </div>
            <ul class="shop-images">
              <li v-for="item in shop.imgs" :key="item.img">
                <a :href="item">
                  <img :src="item">
                </a>
              </li>
            </ul>
          </li>
        </ul>
        <div class="loading-more" v-show="loading">
          <span></span>
        </div>
        <div class="list-finished" v-show="allLoaded">已经没有更多了</div>
      </div>
    </div>
    <div class="go-to-top" v-show="isShowTotop" @click="toTop"></div>
  </div>
</template>
<script>
import searchForm from "@/components/searchForm";

import "velocity-animate/velocity.ui.min.js";
import URL from "js/api.js";
import axios from "axios";
import { InfiniteScroll } from "mint-ui";
import Vue from "vue";
Vue.use(InfiniteScroll);

export default {
  components: { searchForm },
  data() {
    return {
      goodLists: [],
      shopLists: [],
      keyword: "测试商品",
      loading: false,
      allLoaded: false,
      pageNum: 1,
      pageSize: 6,
      showId: 0, // 0 good, 1 shop
      isShowTotop: false,
      query: null
    };
  },
  created() {},
  mounted() {
    this.query = this.$route.query;
    this.showId = this.query.showId;
  },
  watch: {
    "$route"(val) {
      // 防止缓存 不走 beforeCreate
      this.allLoaded = false;
      this.showId = val.query.showId;
      if (this.showId == 1) {
        this.getShops();
      } else if(this.showId === 0){
        this.getGoods();
      }
    },
    query() {}
  },
  methods: {
    toTop() {
      document.documentElement.velocity({
        scrollTop: "0"
      });
      setTimeout(() => {
        this.move();
      }, 0);
    },
    move() {
      let top = document.body.scrollTop || window.scrollY;
      this.isShowTotop = top > 0 ? true : false;
    },
    getShops() {
      if (this.allLoaded) {
        return;
      }
      this.loading = true;
      axios
        .post(URL.searchShops, {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          keyword: "测试店铺"
        })
        .then(res => {
          let lists = res.data.lists;
          if (lists.length < this.pageSize) {
            this.allLoaded = true;
          }
          if (this.shopLists.length) {
            this.shopLists = this.shopLists.concat(lists);
          } else {
            this.shopLists = lists;
          }
          this.loading = false;
          this.pageNum++;
        });
    },
    getGoods() {
      if (this.allLoaded) {
        return;
      }
      this.loading = true;

      axios
        .post(URL.searchGoods, {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          keyword: "测试商品"
        })
        .then(res => {
          let lists = res.data.lists;
          if (lists.length < this.pageSize) {
            this.allLoaded = true;
          }
          if (this.goodLists.length) {
            this.goodLists = this.goodLists.concat(lists);
          } else {
            this.goodLists = lists;
          }
          this.loading = false;
          this.pageNum++;
        });
    }
  }
};
</script>
